
.rectangle {
    width: 160px;
    height: 40px;
    border-radius: 7px;
    border: solid 1px #ffffff;
    float: right;
    margin-right: 20%;
    margin-top: -2%;
}
.Cost-For-Two {
    height: 45px;
    font-family: Poppins;
    font-size: 18px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 3.5;
    letter-spacing: normal;
    text-align: left;
    color: #192f60;
    margin-left: 7%;
}
.item {
    width: 875px;
    height: 320px;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
    background-color: #f2eded;
    margin-bottom: 20px;
}
.option-item {
    font-family: Poppins;
    font-size: 15px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.8;
    letter-spacing: normal;
    text-align: left;
    color: #8c96ab;
    margin-right: 100px;
}
.block {
    display: block;
}
.inline-block {
    display: inline-block;
}
.hotel_name {
    width: 100%;
    height: 43px;
    font-family: Poppins;
    font-size: 25px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: 2;
    letter-spacing: normal;
    text-align: left;
    color: #192f60;
}
.imageclass {
    width: 100%;
    height: 220px;
    border-radius: 23px;
    background-color: #ea6060;
    margin-left:5%;
}
.cityname {
    width: 100%;
    height: 23px;
    font-family: Poppins;
    font-size: 16px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: 2.4;
    letter-spacing: normal;
    text-align: left;
    color: #192f60;
}
.address-text {
    width: 100%;
    height: 23px;
    font-family: Poppins;
    font-size: 16px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 2.88;
    letter-spacing: normal;
    text-align: left;
    color: #636f88;
}
.row-height {
    height: 60%;
    padding-top: 4%;
}
.cuisine_block {
    font-family: Poppins;
    font-size: 16px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.88;
    letter-spacing: normal;
    text-align: left;
    color: #636f88;
    padding-left: 25%;
}
.cuisine_out {
    font-family: Poppins;
    font-size: 16px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.88;
    letter-spacing: normal;
    text-align: left;
    color: #192f60;
}